import React, { useState } from 'react'
import Plantilla from './Plantilla/Plantilla';
import "./productos.css"

const ProductosEnVenta = () => {

    const [productos] = useState([
        {
            name: 'Buzo',
            precio: 1500,
            id: 1,
            thumbnail: "data:image/jpeg;base64,",
        },
        {
            name: 'Campera',
            precio: 2200,
            id: 2,
            thumbnail: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR6_TP2BXeFKRU0q__f9n-ekmrytvS8PnIy9-MMi1f16zWEqd-V74WPztPoa-I_97DcikM&usqp=CAU",
        },
        {
            name: 'Conjunto',
            precio: 3800,
            id: 3,
            thumbnail: "data:image/jpeg;base64,",
        },
        {
            name: 'Gorro',
            precio: 800,
            id: 4,
            thumbnail: "data:image/jpeg;base64,",
        },
        {
            name: 'Taza',
            precio: 450,
            id: 5,
            thumbnail: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTyKtYhckrESWKL2RZEH3iWTqz3-uPGgeD4MA&usqp=CAU",
        },
        {
            name: 'Bufanda',
            precio: 800,
            id: 6,
            thumbnail: "data:image/jpeg;base64,",
        },
        {
            name: 'Llavero',
            precio: 300,
            id: 7,
            thumbnail: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR9Ntib7OgKKT7e26oN5unMQ8pTsQowtDYfWA&usqp=CAU",
        },
        {
            name: 'Chomba',
            precio: 1500,
            id: 8,
            thumbnail: "data:image/jpeg;base64,",
        },
    ]);
    return (
        <main>
            <h2>Tienda de regalos</h2>
            <section id="figuritas">
                {productos.map((producto) => {
                    return (
                        <Plantilla key={producto.id} name={producto.name} thumbnail={producto.thumbnail} precio={producto.precio} id={producto.id} />
                    );
                })}
            </section>
        </main>
    )
}

export default ProductosEnVenta